<template>
    <div>
      <Header :back="false">首页</Header>
      <div class="box">
        <Loading v-if="loading">正在加载中...</Loading>
        <template v-else>
          <Banner :banner="bannerData"></Banner>
          <div  class="container">
            <h5>热门图书</h5>
            <ul>
              <li v-for="(item,index) in bookData" :key="index">
                <img :src="item.bookPic">
                <h5>{{item.bookName}}</h5>
                <p>{{item.bookInfo}}</p>
              </li>
            </ul>
          </div>
        </template>
      </div>
    </div>
</template>

<script>
  import Header from "../base/Header"
  import Banner from "../base/Banner"
  import Loading from "../base/Loading"




  //import {getBanner, getBooks} from "../api"
  import {getAll} from "../api"

  export default {
      created() {
        // this.getBannerData();
        // this.getHotBookData();
        this.getData()
      },
      data() {
          return {
            bannerData: [],
            bookData: [],
            loading: true
          }
      },
      methods: {
        // async getBannerData() {
        //   this.bannerData = await getBanner();
        // },
        // async getHotBookData() {
        //   this.bookData = await getBooks();
        // }
        async getData() {
          let [banner, hotBooks] = await getAll();
          this.bannerData = banner;
          this.bookData = hotBooks;
          //轮播图和热门图书获取完成
          this.loading = false;
        }
      },
      computed: {},
      components: {
        Header,
        Banner,
        Loading
      },
  }
</script>

<style scoped lang="less">
  .box{
    position: absolute;
    top: 40px;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .container{
    width: 95%;
    margin: 0 auto;
    h5{
      color: #aaa;
    }
    ul{
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 60px;
      li{
        width: 50%;
        text-align: center;
        img{
          width: 100%;
        }
        h5{
          color: #000;
        }
        p{
          font-size: 14px;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }
  }
</style>



